import React from 'react'
import Carditem from './Carditem'
import image1 from '../../preimages/img-1.jpg'
import image2 from '../../preimages/img-2.jpg'
import image3 from '../../preimages/img-3.jpg'
import image4 from '../../preimages/img-4.jpg'
import image9 from '../../preimages/img-9.jpg'

function Cards() {
    return (
        <div className='cards'>
            <h1 className="">Check out these epic destination</h1>
            <div className="cards__container">
                <div className="cards__wrapper">
                    <ul className="cards__items">
                        <Carditem
                        src={image9}
                        text='Explore the hidden waterfall' 
                        label='Adventure'
                        path='/services' />
                        <Carditem
                        src={image1}
                        text='Explore the beauty of Taj Mahal' 
                        label='Adventure'
                        path='/services' />
                    </ul>
                    <ul className="cards__items">
                        <Carditem
                        src={image2}
                        text='Explore Lotus Temple' 
                        label='Adventure'
                        path='/services' />
                        <Carditem
                        src={image3}
                        text='Explore Mysore palace' 
                        label='Adventure'
                        path='/services' />
                        <Carditem
                        src={image4}
                        text='Explore Golden Temple' 
                        label='Adventure'
                        path='/services' />
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default Cards
